@import "https://fonts.googleapis.com/css2?family=Rubik:wght@400;700&display=swap";
@import "https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700;800&display=swap";
*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Poppins, sans-serif;
}

h1, h2, h3, h4, h5, h6 {
  text-align: center;
  font-family: Rubik, sans-serif;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.container {
  width: 100%;
  max-width: 1312px;
  margin: 0 auto;
  padding: 0 16px;
}

.overflow {
  overflow: hidden;
}

.header-nav-mob {
  width: 100%;
  height: 100%;
  padding: 14px 10px 14px 54px;
  display: none;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.header-nav-mob.active {
  z-index: 3;
  display: block;
}

.header-nav-mob__overlay {
  width: 100%;
  height: 100%;
  opacity: 0;
  background: linear-gradient(0deg, #22253be6, #22253be6);
  transition: opacity .3s;
  position: absolute;
  inset: 0;
}

.header-nav-mob.active .header-nav-mob__overlay {
  opacity: 1;
}

.header-nav-mob__wrapp {
  width: 100%;
  height: 100%;
  background-color: #fff;
  border-radius: 20px;
  flex-flow: column;
  padding: 22px 16px 34px 31px;
  display: flex;
  position: relative;
  transform: translateX(100%);
}

.header-nav-mob.active .header-nav-mob__wrapp {
  transform: translateX(0);
}

.toggle-menu--close {
  width: 20px;
  height: 20px;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  display: flex;
  position: relative;
}

.toggle-menu--close span {
  width: 19px;
  height: 1px;
  background: #bcbcbc;
  display: block;
  transform: rotate(-135deg);
}

.toggle-menu--close span:last-of-type {
  position: absolute;
  bottom: 9px;
  left: 1px;
  transform: rotate(-45deg);
}

.header-nav-mob__list {
  margin-top: 17px;
  margin-bottom: 0;
  padding-left: 0;
  font-size: 20px;
  list-style: none;
}

.header-nav-mob__list li {
  margin-bottom: 21px;
}

.header-nav-mob__link {
  color: #4f5665;
}

.menu-batton {
  color: #f53855;
  border: 1px solid #f53855;
  border-radius: 50px;
  padding: 13px 20px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.2;
  transition: all .2s ease-in-out;
}

.menu-batton:hover {
  color: #fff;
  background-color: #f53855;
}

.header-nav-mob .menu-batton {
  height: 48px;
  width: 220px;
  fill: #0000;
  justify-content: center;
  margin-top: auto;
  font-size: 15px;
}

.header-nav-mob .menu-batton svg {
  margin-right: 8px;
}

